$sidebar-nav-item-padding: $space-one-third;

.block-region-sidebar {
  .menu-item {
    list-style: none;
  }

  .menu-level-0 {
    font-family: $font-tertiary;
    font-size: 1.5rem;
    line-height: 1.5;
    margin: -10px 0 0;
    padding-left: 40px;

    a {
      display: block;
      color: $grey-7;
      text-decoration: none;
      padding: $sidebar-nav-item-padding 25px $sidebar-nav-item-padding $sidebar-nav-item-padding;
    }

    a:hover,
    a.is-active {
      background: $mushroom-lighter;
      color: $burnt-orange;
    }

    > .menu-item--expanded {
      > a::after {
        // content: "\203a";
        // padding-right: 10px;
        // // font-size: 1.2em;
        // vertical-align: middle;
        // color: $mushroom-lighter;
        // font-weight: bold;
        // right: 0;
        // position: absolute;
      }

      &.menu-item--active-trail {
        > a::after {
          // transform: rotate(90deg);
          // padding-right: 0;
          // right: 10px;
        }
      }

      a:hover::after {
        color: $burnt-orange;
      }
    }

    .menu-item--expanded {
      .menu-dropdown {
        display: none;
      }

      &.menu-item--active-trail {
        .menu-dropdown {
          display: block;
        }
      }
    }
  }

  .main-menu--level-1 {//only needed because of bug in menu_item_extras when caching enabled
    max-height: initial;

    a {
      padding-left: $sidebar-nav-item-padding * 2;
    }

    > .menu-item--active-trail {
      > a {
        color: $grey-8;
      }

      > a:hover {
        color: $burnt-orange;
      }
    }
  }
}

.side-menu {
  @at-root ul#{&} {
    padding-left: $space-two-thirds;
    width: 100%;
  }

  &__item {
    list-style: none;
    position: relative;
    // Items with children

    .menu-item--active-trail > a {
      color: $burnt-orange;
    }

    &--with-sub {
      display: flex;
      flex-flow: row wrap;

      @include breakpoint($break-nav-max) {
        overflow: hidden;
      }

      @include breakpoint($break-nav) {
        flex-flow: column nowrap;

        &.main-menu__item--level-0 {
          &:hover {
            > .main-menu__dropdown {
              // display: block;
              // left: 0;
              // position: absolute;
              // top: 100%;
              // width: 100%;
              // z-index: 1;
            }
          }
        }
      }
    }
  }

  &__expand-sub {
    // align-items: center;
    cursor: pointer;
    // display: flex;
    // flex: 0 0 $side-menu-dropdown-toggle-width;

    padding: $space-one-third $space-one-third $space-one-third 0;
    // font-size: 1.2em;
    vertical-align: middle;
    font-weight: bold;
    right: 0;
    position: absolute;

    @include breakpoint($break-nav) {
      // display: none;
    }
  }

  &__sub-icon {
    fill: $grey-c;
    height: 1rem;
    width: 1rem;

    &.side-menu {
      padding: 0;
    }

    &:hover {
      fill: $burnt-orange;
    }

    &--open {
      fill: $grey-c;
      transform: rotate(90deg);
    }
  }

  &__dropdown {
    display: none;
    // min-width: 300px;
    // background-color: $color-menu-bg-dark;

    &.js-visible {
      // @include breakpoint($break-nav-max) {
      display: flex;
      flex-basis: 100%;
      // }
    }
  }

  // &__dropdown-inner {
  //   display: flex;
  //   flex-flow: row nowrap;
  //   width: 100%;
  //
  //   @include breakpoint($break-nav) {
  //     @include wrapper;
  //   }
  // }
}
