.text-long {
  table {
    font-family: $font-tertiary;
    line-height: 1.875em;

    thead {
      font-size: 1.063em;
      text-align: left;
      background: #f1f1f1;
      padding: 20px 15px;
    }

    tbody {
      border-bottom: 1px solid #e6e6e6;
      padding: 20px 0;

      td {
        color: $grey-7;
      }
    }
  }
}

.wysiwyg {
  &__table {
    font-family: $font-tertiary;
    line-height: 1.875em;
  }

  &__tbody {
    border-bottom: 1px solid #e6e6e6;
    padding: 20px 0;
  }

  // &__tr {
  //
  // }

  &__td {
    color: $grey-7;
  }
}
