body {
  position: relative;
  font-size: 1.6rem;
  background-color: #f8f9f7;
}

.main {
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
  padding: 0 15px;
  padding: 30px 0;
  display: block;
  background-color: #fff;
}

@media (min-width: 480px) {
  .main {
    padding: 0 15px;
  }
}

@media (min-width: 1200px) {
  .main {
    padding-left: calc(15px + calc(-50vw + calc(1200px / 2)));
    padding-right: calc(15px + calc(-50vw + calc(1200px / 2)));
  }
}

.noscroll {
  overflow: hidden;
  position: fixed;
  width: 100%;
}

.embedded-entity__image {
