    width: 40%;
  }
}

.embedded-entity__image.align-left {
  clear: left;
  margin-bottom: 3%;
  margin-right: 5%;
  width: 33%;
}

@media (min-width: 768px) {
  .embedded-entity__image.align-left {
    width: 25%;
  }
  .embedded-entity__image.align-left.wp-import {
