  }
}

.sg-main {
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
  padding: 0 15px;
}

@media (min-width: 480px) {
  .sg-main {
    padding: 0 15px;
  }
}

@media (min-width: 1200px) {
  .sg-main {
    padding-left: calc(15px + calc(-50vw + calc(1200px / 2)));
    padding-right: calc(15px + calc(-50vw + calc(1200px / 2)));
  }
}

.pl-template {
  display: flex;
  flex-flow: column nowrap;
}

.pl .sg-subtype h2,
.sg-main .sg-subtype h2 {
  font-size: 3rem;
  text-transform: uppercase;
  background-color: #444;
  margin: 0 calc(-50vw + 50%) 30px calc(-50vw + 50%);
}

.pl .sg-subtype h2 a,
.sg-main .sg-subtype h2 a {
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
  padding: 30px 15px;
  display: block;
  color: #fff;
  text-decoration: none;
  line-height: 1;
}

@media (min-width: 480px) {
  .pl .sg-subtype h2 a,
  .sg-main .sg-subtype h2 a {
    padding: 30px 15px;
  }
}

@media (min-width: 1200px) {
  .pl .sg-subtype h2 a,
  .sg-main .sg-subtype h2 a {
    padding-left: calc(15px + calc(-50vw + calc(1200px / 2)));
    padding-right: calc(15px + calc(-50vw + calc(1200px / 2)));
  }
}

.pl .sg-pattern-title,
.sg-main .sg-pattern-title {
  color: #738ba3;
  font-size: 1.4rem !important;
  text-transform: none !important;
}

.pl .sg-pattern-title a,
.sg-main .sg-pattern-title a {
  color: #1169ba !important;
  font-family: Calibre;
  font-size: 2.2rem;
  font-weight: 500;
  text-transform: uppercase !important;
}

.pl .sg-pattern-extra-info,
.sg-main .sg-pattern-extra-info {
  width: 100%;
}

.pl .sg-pattern-extra-code,
.sg-main .sg-pattern-extra-code {
  display: none;
}

.pl .visually-hidden,
.sg-main .visually-hidden {
  position: absolute !important;
  clip: rect(1px, 1px, 1px, 1px);
  overflow: hidden;
  height: 1px;
  width: 1px;
  word-wrap: normal;
}

.pl .sg-colors,
.sg-main .sg-colors {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  padding: 0;
  margin: 0;
}

.pl .sg-colors li,
.sg-main .sg-colors li {
  flex: 0 0 100px;
  margin: 0 0.5rem 1rem;
}

.pl .sg-colors__sample,
.sg-main .sg-colors__sample {
  display: block;
  height: 100px;
  margin-bottom: 0.3em;
}

