  justify-content: space-between;
  align-items: center;
}

@media (min-width: 480px) {
  .header-region {
    padding: 0 15px;
  }
}

@media (min-width: 1200px) {
  .header-region {
    padding-left: calc(15px + calc(-50vw + calc(1200px / 2)));
    padding-right: calc(15px + calc(-50vw + calc(1200px / 2)));
  }
}

@media (min-width: 900px) {
  .header-region {
    flex-wrap: wrap;
  }
}

.header__top {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;
  flex: 1 1 75%;
  margin: 0 40px;
}

.header__branding {
  flex: 1 1 50%;
}

.header__branding img {
  max-width: 288px;
}

.header__secondary {
  flex: 1 1 50%;
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-end;
  text-align: right;
  padding: 1em 0;
}

/* Not sure where these kinds of rules belong, so here they are. */
.image-caption,
.image-credit {
  font-family: "Rubik", sans-serif;
  display: inline-block;
  font-weight: 400;
  color: #999;
  line-height: 19px;
  margin-top: 1em;
}

.image-credit {
  font-size: 12px;
  color: #ccc;
  text-transform: uppercase;
  margin-left: 0.5em;
}
